                      <div style="font-size:smaller">
                          <h4> Wie wird der Anteil der BAfG-Empfnger*innen berechnet?</h4>
                          <p>
                              Um den An&shy;teil der Stu&shy;dieren&shy;den zu be&shy;rech&shy;nen, die durch BAfG ge&shy;fr&shy;dert wer&shy;den, wer&shy;den zwei Da&shy;ten&shy;stze des sta&shy;tis&shy;tischen Bun&shy;des&shy;amts kom&shy;bi&shy;niert:
                              "Studierende: Bundeslnder, Semester, Nationalitt, Geschlecht" (Studierendendatensatz) und "BAfG - Gefrderte Personen und finanzieller Aufwand: Bundeslnder, Jahre, Personenkreis" (BAfG-Datensatz).
                              Da&shy;bei ent&shy;hlt der BAfG-Daten&shy;satz die ab&shy;solu&shy;te Zahl der in einem Ka&shy;len&shy;der&shy;jahr ge&shy;fr&shy;derten Stu&shy;dierenden.
                              Der Stu&shy;die&shy;renden&shy;daten&shy;satz lie&shy;fert fr das Winter&shy;se&shy;mester des sel&shy;ben Jah&shy;res die An&shy;zahl der Stu&shy;dieren&shy;den mit deutscher Staats&shy;br&shy;ger&shy;schaft.
                              Durch die Di&shy;vi&shy;sion bei&shy;der Wer&shy;te er&shy;gibt sich der An&shy;teil der BAfG-Empfnger*innen auf Bundes- bzw. Lnder&shy;ebene fr das je&shy;wei&shy;lige Jahr.
                          </p>

                          <h4> Weshalb werden Prozente und keine absoluten Zahlen angegeben?</h4>
                          <p>
                              In der me&shy;dia&shy;len Be&shy;richt&shy;er&shy;stattung zum BAfG fin&shy;den sich hufig Aus&shy;sagen zur ab&shy;soluten Zahl der BAfG-Empfnger*innen.
                              Bei der The&shy;matik sind sol&shy;che An&shy;gaben je&shy;doch nur be&shy;schrnkt aus&shy;sage&shy;krf&shy;tig, da den meis&shy;ten Le&shy;senden nicht bekannt sein drfte, wie vie&shy;le Stu&shy;dierende es in Deutsch&shy;land gibt.
                              Be&shy;dingt durch Ver&shy;n&shy;derungen im Bildungs&shy;system sind weiter&shy;hin die Stu&shy;dierenden&shy;zah&shy;len im Zeit&shy;raum von 1998 bis 2018 von 1,6 Millionen deutscher Studierende auf 2,5 Millionen an&shy;ge&shy;stie&shy;gen (eigene Be&shy;rech&shy;nung: Stu&shy;dierenden&shy;daten&shy;satz).
                              Durch Prozent&shy;an&shy;gaben kann somit gegen&shy;ber abso&shy;luten Zah&shy;len eine Vergleichs&shy;grund&shy;lage ge&shy;schaf&shy;fen wer&shy;den.
                          </p>

                          <h4>Wieso wird nur die Anzahl der deutschen Studierenden betrachtet?</h4>
                          <p>
                              In den ver&shy;gan&shy;ge&shy;nen Jah&shy;ren ist auch die Zahl der aus&shy;ln&shy;dischen Stu&shy;dieren&shy;den stark an&shy;ge&shy;stie&shy;gen. Mit&shy;glieder dieses Personen&shy;kreises kn&shy;nen je&shy;doch nur in Aus&shy;nahme&shy;fllen BAfG be&shy;an&shy;tra&shy;gen.
                              Um die Zahl der Stu&shy;die&shy;ren&shy;den, die theo&shy;re&shy;tisch BAfG be&shy;an&shy;tra&shy;gen kn&shy;nen, mglichst gut ab&shy;zuschtzen, wur&shy;den daher nur Stu&shy;die&shy;rende mit deutscher Staats&shy;brger&shy;schaft in die Be&shy;rech&shy;nung ein&shy;be&shy;zogen.
                              Durch diese Ma&shy;nah&shy;me wird der An&shy;teil der BAfG-Empfnger*innen ten&shy;denziell ber&shy;schtzt.
                          </p>

                          <h4>Welche alternativen Berechnungsmethoden gibt es?</h4>
                          <div style="text-align: center; margin-top: 6px;">
                              <img style="width: 100%" src="../assets/berechnung.jpg">
                              <img style="width: 50%" src="../assets/berechnung_leg.jpg">
                          </div>
                          <p>
                              Neben der von uns verwendeten Berechnung (obere Linie, hellblau), gibt es weitere alternative Methoden zur Bestimmung der Gefrdertenquoten (Daten aus Iost, 2019).
                              Beispielsweise lsst sich die Gefrdertenquote unter allen Studierenden betrachten (untere Linie, dunkelblau).
                              Aufgrund der hier enthaltenen auslndischen Studierenden ist die errechnete Gefrdertenquote unter allen Studierenden wesentlich niedriger als in unseren Berechnungen.
                              Im Rahmen der Berichte der Bundesregierung nach 35 BAfG wird weiterhin eine Gefrdertenquote unter allen Anspruchsberechtigten bestimmt (mittlere Linie, mittelblau).
                              Diese soll theoretisch den Anteil der gefrderten Studierenden mglichst gut abbilden.
                              Allerdings schwanken die Definitionskriterien fr Anspruchsberechtigte zwischen den Berichten.
                              Insgesamt wird jedoch ersichtlich, dass wir duch unsere Berechnungsmethodik die BAfG-Quote systematisch berschtzen.
                              Qualitativ zeigt sich fr alle drei Berechnungsmethoden eine hnliche Verlaufsform fr die Entwicklung des Anteils der BAfG-Empfnger*innen.
                          </p>
                      </div>
