                           <br>
                           <div style="font-size:smaller">
                               Beide Datenstze stammen aus serisen Quellen.
                               Insbesondere bei staatlichen Finanzdaten zum BAfG ist davon auszugehen, dass die Daten mit besonderer Sorgfalt erhoben wurden.
                               Beide Datenstze sind vollstndig und weisen somit keine fehlenden Werte auf. Das Datenformat ist maschinenlesbar und liegt bereits in einem "tidy"-Format vor.
                               Hinsichtlich der Kompatibilitt ist zu beachten, dass der BAfG-Datensatz sich auf das Kalenderjahr bezieht, wohingegen die Studierendendaten jeweils nur fr das Wintersemester erhoben wurden.
                               Das bedeutet, dass die Verknpfung der beiden Datenstze mit einer Ungenauigkeit einhergeht, die sich weder gut bestimmen noch beheben lsst.
