        <br><br><br>

          <div style="text-align: justify">

              Im Ver&shy;lauf der Zeit ist ein Unter&shy;schied zwi&shy;schen den neu&shy;en und al&shy;ten Bun&shy;des&shy;ln&shy;dern bei der Fr&shy;der&shy;quote er&shy;kennbar.
              Bei der Aus&shy;wahl des Studie&shy;nortes bleibt die Mehr&shy;heit der Studie&shy;renden im ei&shy;genen Bundes&shy;land oder im Nach&shy;bar&shy;bun&shy;des&shy;land.
              Daher lsst sich ver&shy;muten, dass die niedri&shy;geren Ein&shy;kommen im Os&shy;ten fr einen Teil der hhe&shy;ren Frder&shy;quote ver&shy;ant&shy;wortlich sind.
              Bei den west&shy;lichen Bundes&shy;lndern bil&shy;den Schles&shy;wig-Holstein, Saar&shy;land, Nieder&shy;sachsen und Rhein&shy;land-Pfalz die Schluss&shy;lichter bei den durch&shy;schnitt&shy;lichen Ein&shy;kommen.
              Dies spie&shy;gelt sich bei den BAfG-Frder&shy;quoten ent&shy;sprechend eben&shy;falls wieder, be&shy;sonders ab etwa 2005.
              Das Saar&shy;land bil&shy;det hierbei eine Au&shy;nahme auf&shy;grund der Gr&shy;e des Bundes&shy;landes.
              Es gibt hier nur eine Uni&shy;ver&shy;sitt und dem&shy;ent&shy;spre&shy;chend ver&shy;hltnis&shy;mig wenig Studie&shy;rende.
              Aus&shy;reier knnen hier&shy;bei strker ins Ge&shy;wicht fal&shy;len.


