        width: 735px;
        height: 172px;
        left: 85px;
    }
    #perc20 {
        text-align: left;
        position: relative; top: 1.8vw;
    }
    #perc25 {
        text-align: left;
        position: relative; top: -0.8vw;
    }
    #perc30 {
        text-align: left;
        position: relative; top: -2.5vw;
    }
    #dev_graph .descr {
        left: 85px!important;
        width: 700px !important;
    }
    #dev_graph  .v-input.theme--light.v-input__slider {
        position: relative;
        left: 85px;
        width: 735px !important;
    }
    #dev_graph {
        display: inline-block;
        margin: 0 auto;
    }
}


/*###########################################################*/

.year-texts {
    font-size: 15px;
    text-align: left;
}

.legend {
    font-size: 10px;
    margin: 10px 0px 20px;
}
.legend-color {
    position: relative;
    top: 7px;
    margin-right: 5px;
    height: 20px;
    width: 20px;
    border: 1px solid black;
    display: inline-block;
}

@media (max-width: 599px) {
    .legend div {
        display: inline-block;
        margin: 0px 0px 10px 20px;
    }
    .legend .legend-color {
        display: inline-block;
        margin: 0px 5px 0px 0px;
    }
}


</style>

<script>
const yearsShort = [
          '98',
          '99',
          '00',
          '01',
          '02',
          '03',
          '04',
          '05',
          '06',
          '07',
          '08',
          '09',
          '10',
          '11',
          '12',
          '13',
          '14',
          '15',
          '16',
          '17',
          '18'
          ]


export default {
  name: 'Development',

  data: () => ({
    sheet_ost: false,
    sheet: false,
    verlauf: false,
    slider: {
        value: 0,
        ticksLabels: yearsShort
    },
    percOverYears: {
      width: 1,
      radius: 1,
      padding: 8,
      lineCap: 'round',
      gradient: ['#0288D1','#81D4FA'],
